@charset "utf-8";

/* ================================================== S : FEED CSS ===================================================  */

.wrap_search+.feed-list {
    margin-top: rem(20);
}

@mixin afterBluer {
    overflow: hidden;
    height: rem(350);
    position: relative;

    &:after {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: rem(30);
        display: block;
        background: linear-gradient(180deg, rgba(255, 255, 255, 0.20) 0%, #FFF 100%);
    }
}

.feed-list {
    .feed-item {
        &:first-child {
            margin-top: rem(40);
        }

        .list-desc {
            @include afterBluer;
        }

        .load-wrap {
            @include afterBluer;

            .list-desc {
                height: auto;

                &:after {
                    display: none
                }
            }
        }
    }
}

.feed-item {
    border-bottom: rem(10) solid #F1F2F5;
    padding: 0 rem(20) rem(20);
    margin: 0 rem(-20);

    &.no-border-bottom {
        border-bottom: 0 !important;
    }

    +.feed-item {
        margin-top: rem(40);
    }

    &.write-page {
        .load-wrap {
            border-bottom: rem(1) solid #e5e5e5;
        }

        .layer_memo_wrap {
            border-top: 0;
        }
    }
}

.layer_cont .feed-item {
    padding: rem(20) 0;
    border-bottom: rem(1) solid #f1f2f5;
    margin: 0;
}

.feed-item .compare_chart {
    margin-top: rem(50);

    &.feed-bar-chart {
        margin-top: rem(30);
    }
}

.list-name-box {
    color: #434655;
    display: flex;
    font-size: rem(14);
    font-weight: 400;
    align-items: center;
    justify-content: space-between;
    width: 100%;

}

.list-name {
    color: #434655;
    display: flex;
    font-size: rem(14);
    font-weight: 400;
    align-items: center;

    >img {
        margin-right: rem(5);
    }

    &.flexS {
        justify-content: flex-start !important;
    }

    .list-name-box {
        justify-content: flex-start;

        img {
            border-radius: 50%;
            margin-right: rem(5);
            display: block;
            height: rem(30);
            width: rem(30);
        }
    }

    &.hasLine {
        padding-bottom: rem(20);
        border-bottom: rem(1) solid #F1F2F5;
    }
}

.feed-item .list-name-box .list-name>img {
    border-radius: 50%;
    margin-right: rem(5);
    display: block;
    height: rem(30);
    width: rem(30);
}

.list-name.flexS>img {
    margin-right: rem(5);
}

.feed-item {
    .list-title {
        color: #333;
        font-size: rem(16);
        font-weight: 700;
        line-height: 1.5;

        &.underLine {
            border-bottom: rem(1) solid #F1F2F5;
            padding-bottom: rem(10);
        }

        +.load-wrap {
            margin-top: rem(24);

            .accItem i {
                background: #fff;
            }

            .accItem input[type="checkbox"] {
                display: none;
            }

            .asList.type_info {

                input,
                i {
                    display: none
                }
            }
        }
    }

    .list-desc {
        color: #666;
        font-size: rem(16);
        font-weight: 400;
        line-height: 1.5;
        word-break: keep-all;
    }

    .list-util {
        display: flex;
        justify-content: space-between;
        align-items: center;

        &.jcfe {
            justify-content: flex-end;
        }
    }

    .list-util-left {
        display: flex;

        >div {
            display: flex;
            align-items: center;

            &+div {
                margin-left: rem(18);
            }
        }
    }

    .likes-num,
    .comment-num,
    .list-date {
        color: #999;
        font-size: rem(16);
        font-weight: 400;
        margin-left: rem(5);
    }

    .likes-num-btn {
        background: url(../img/theme/apthefin/icon_thumb.png) no-repeat 0 0;
        cursor: pointer;
        height: rem(20);
        width: rem(20);
        background-size: cover;

        &.on {
            background: url(../img/theme/apthefin/icon_thumb_on.png) no-repeat 0 0;
            background-size: cover;

            &+.likes-num {
                color: #1c64f2
            }
        }
    }

    .comment-num-btn {
        background: url(../img/theme/apthefin/icon_comment.png) no-repeat 0 0;
        cursor: pointer;
        height: rem(20);
        width: rem(20);
        background-size: cover;

        &.on {
            background-image: url("../img/theme/apthefin/icon_comment_on.png");

            ~.comment-num {
                color: #1c64f2
            }
        }
    }

    .list-name+ {

        .list-title,
        .list-desc {
            margin-top: rem(20);
        }
    }

    .list-title+.list-desc {
        margin-top: rem(10);
    }

    .list-desc+.list-util {
        margin-top: rem(12);
    }

    .feed-btn-more {
        background: url(../img/theme/apthefin/icon_more.png) no-repeat 0 0;
        cursor: pointer;
        height: rem(24);
        width: rem(24);
        @include txtHidden();
        display: block;
    }

    .load-wrap {
        &.padd20 {
            padding: rem(20) 0;

            .accItem input[type="checkbox"] {
                display: none;
            }

            .accItem i {
                background: #fff;
            }
        }

        +.list-util {
            margin-top: rem(26);
        }

        .asGroup .asList .amount:after {
            background: none;
        }
    }

    .asGroup+.asGroup {
        margin-top: rem(10);
        padding-top: 0;

        > {
            .asList {
                margin-top: rem(10);
            }

            .listTit {
                display: none;
            }
        }
    }

    .compare_chart .consumeAsList.fold {
        padding: 0;
    }
}

.compare_chart .consumeAsList.show {
    padding: 0;
}

.feed-item {
    .asGroup.compare_chart .asList {
        border: none;
        border-radius: 0;
        box-shadow: none;
    }

    .compare_chart .wrapBarGraph .wrapBar_detail {
        border: 0;
        border-top: rem(1) solid #E5E5E5;
        border-style: dashed;
        margin-top: rem(30);
        padding-top: rem(10);
    }

    .bar-data {
        color: #fff;
        font-size: rem(13);
        font-weight: 700;
    }

    .compare_chart .horizontal .wrapBar .gBarBox {
        .gBar {
            border-radius: rem(0) rem(4) rem(4) rem(0) !important;
            text-align: right;
            height: rem(24);
            padding-right: rem(10);
            line-height: rem(24);
        }

        .myAsset {
            height: rem(24);
        }
    }
}

.compare_chart .horizontal .wrapBar .gBarBox .otherAsset {
    height: rem(24);
}

.feed-item .compare_chart .wrapBarGraph .wrapBar_detail .bar_rate {
    &.blue:before {
        background-color: #78C889;
    }

    &.gray:before {
        background-color: #DBDBDB;
    }
}

.layer_fullsheet.feed {
    height: 100vh;
    margin-bottom: 0;

    .layer_cont {
        height: 100%;
        overflow: auto;

        &.hasFixBottom {
            padding-bottom: rem(150);
        }
    }
}

/* ================================================== E : FEED CSS ===================================================  */

/* S : input CSS */

input[type="password"] {
    font-family: caption, sans-serif;
    font-size: rem(22);
}

.input-wrap {
    margin: rem(20) 0 0;
    width: 100%;
    position: relative;
}

.desc-wrap {
    &.flex {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    + {
        .input-wrap {
            margin-top: rem(56);
        }

        .accSetting {
            margin-top: rem(10);
        }
    }
}

.input-wrap {
    &.fixed {
        position: fixed;
        bottom: 0;
        left: 0;
        padding-bottom: rem(15);
        background: #fff;
    }

    input[type="text"] {
        height: rem(54);
        padding: rem(15) rem(45) rem(15) rem(20);
    }
}

.input-box {
    position: relative;
}

.list-name+.input-box {
    margin-top: rem(10);
}

.input-box.items2 {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &.hasUnit input {
        padding-right: rem(50)
    }
}

.itemTit+.input-box.items2 {
    margin-top: rem(10);
}

.ipt-pass-box {
    width: 100%;
    position: relative;
    height: rem(42);

    input[type="password"] {
        position: absolute;
        left: 0;
        top: 0;
        z-index: 10;
        opacity: 1;

        &:focus+.password-disc-wrap {
            border-bottom: rem(1) solid #333;
        }
    }
}

.password-disc-wrap {
    background-color: #fff;
    border-bottom: rem(1) solid #ddd;
    height: rem(42);
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    z-index: 1;
}

.pass-disc {
    background-color: #000;
    border-radius: 50%;
    display: inline-block;
    height: rem(10);
    width: rem(10);

    +.pass-disc {
        margin-left: rem(5);
    }
}

.ipt-label {
    font-size: rem(14);
    font-weight: 700;
}

.ipt-typeA {
    /* feed 댓글달기에 들어가는 댓글 입력 input */
    border: 0;
    border-top: rem(1) solid #E5E5E5;
    color: #333;
    font-weight: 400;
    font-size: rem(16);
    padding: 0;
    width: 100%;
}

.ipt-typeB {
    /* feed 댓글달기에 들어가는 댓글 입력 input */
    border: 0;
    border-bottom: rem(1) solid #ddd;
    color: #333;
    font-weight: 400;
    font-size: rem(16);
    padding: 0;
    width: 100%;
}

.ipt-time-limit {
    position: absolute;
    right: 0;
    bottom: rem(10);
    font-size: rem(14);
    font-weight: 400;
}

.input-err-desc {
    color: #C81E1E;
    font-size: rem(13);
    margin-top: rem(10);
}

.ipt-search-desc {
    color: #666;
    font-size: rem(14);
    font-weight: 400;
}

.asGroup+.feed-write-wrap {
    border-top: rem(1) solid #F1F2F5;
    margin-top: rem(24);
}

.load-wrap {
    .feed-write-wrap:first-child {
        margin-top: rem(-24);
    }
}

.feed-write-wrap {
    color: #666;
    font-size: rem(16);
    line-height: rem(22);
    white-space: pre-wrap;
    word-break: break-all;
    word-wrap: break-word;
}

.input-box {
    input {
        color: #333;
        font-weight: 400;
        font-size: rem(16);
        padding: 0;
        border: 0;
        height: rem(42);
        border-bottom: rem(1) solid #ddd;
        width: 100%;
    }

    .layer_memo_wrap {
        border: 0;
        border-top: rem(1) solid #E5E5E5;
        margin-top: 0;
        padding: rem(20) 0;
        min-height: auto;
    }
}

.sel_form+.input-box {
    margin-top: rem(10);
}

.input-box+.input-box {
    margin-top: rem(6);
}

.list-name+.sel_form {
    margin-top: rem(10);
}

.sel_form.no-border {
    border: 0;
    border-bottom: rem(1) solid #DDE3E7;
    border-radius: 0;

    >.sel_box {
        padding: rem(15) 0;
    }
}

.accSetting.anima {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;

    .editName {
        transition: all 0.5s;
        height: 0;
        overflow: hidden;
        margin: rem(15) 0;

        &:nth-of-type(1) {
            order: 4;
        }

        &:nth-of-type(2) {
            order: 3;
        }

        &:nth-of-type(3) {
            order: 2;
        }

        &:nth-of-type(4) {
            order: 1;
        }

        &.open {
            transition: all 0.5s;
            height: rem(66);
            overflow: hidden;
        }

        &.close {
            height: 0;
            opacity: 0;
            margin: 0;
        }
    }
}

.chkLabel-type-btn {
    border: rem(1) solid #E5E5E5;
    border-radius: rem(6);
    color: #666;
    display: block;
    font-size: rem(14);
    font-weight: 500;
    line-height: 1.5;
    padding: rem(10) 0;
    text-align: center;
    width: 100%;
}

input[type="checkbox"]:checked+.chkLabel-type-btn {
    color: #1c64f2;
    border: rem(1) solid #1c64f2;
    font-weight: 700;
}

.itemTit+.input-box {
    //margin-top: rem(10); 높이값이 변경된거 같습니다.
}

