    }

    &:not(:first-of-type) {
        margin-top: rem(4);
    }

    &:before {
        content: '·' !important;
        display: inline-block;
        font-size: rem(18);
        line-height: rem(23);
        position: absolute;
        left: 0;
        top: rem(-5);
    }
}

.list-typeC {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;

    li {
        display: flex;
        align-items: center;
        font-size: rem(14);
        color: #666;
        height: rem(24);
        line-height: 1.5;
        font-weight: 500;
        width: 50%;
        padding-left: rem(34);
        position: relative;

        &:nth-child(n+3) {
            margin-top: rem(16);
        }

        &.bank:before {
            content: '';

