@charset "UTF-8";

$gray : #6f7a81;
$black : #17181A;

.plan {
  &Tit {
    font-size: rem(20);
    line-height: 1.4;
    font-weight: 500;
    color: $black;
    position: relative;

    &.-bold {
      font-weight: 600
    }

    &__bold {
      font-size: rem(22);
    }

    strong {
      font-weight: 700;
    }

    span {
      position: relative;
      color: #1c64f2;

      &:before {
        content: "";
        width: 100%;
        height: rem(4);
        background: #C3DDFD;
        position: absolute;
        left: 0;
        bottom: rem(4);
        z-index: -1;
      }
    }

    &Dec {
      margin-top: rem(2);
      font-size: rem(14);
      line-height: 1.4;
      font-weight: 500;
      color: $gray;
    }

    &Sub {
      font-size: rem(18);
      color: $black;
      font-weight: 700;
      line-height: 1.6;

      span {
        position: relative;
        color: #1C64F2;

        &.-underLine:before {
          content: "";
          width: 100%;
          height: rem(4);
          background: #C3DDFD;
          position: absolute;
          left: 0;
          bottom: rem(4);
          z-index: -1;
        }
      }
    }

    .btn_switch {
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  &__hr {
    height: rem(16);
    background: #F5F7F9;
    display: block;
    border: none;
    margin: rem(30) rem(-20);
    padding: 0 rem(20);
  }

  &__btnTooltip {
    display: inline-block;
    @include iconTooltip();
    @include txtHidden();
    line-height: rem(22);
  }

  &__txtCaution {
    font-size: rem(12);
    line-height: 1.3;
    color: $gray;
  }
}

.planRate {
  position: relative;

  &__inner {
    position: relative;
    padding-bottom: rem(20);
  }

  &__txt {
    position: absolute;
    font-size: rem(12);
    color: #1C64F2;
    font-weight: 700;
    bottom: 0;

    &.-start {
      left: 0;
      text-align: left;
    }

    &.-end {
      right: 0;
      text-align: right;
      color: #3F83F8;
    }
  }

  &__img {
    position: absolute;
    width: 35.8125%;
    height: auto;
    aspect-ratio: 160 / 130;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);

    &[data-per^="0"],
    &[data-per^="1"] {
      background-image: url("../img/theme/apthefin/svg/img_plan_0per.svg");
      background-repeat: no-repeat;
      background-size: cover;
    }

    &[data-per^="2"],
    &[data-per^="3"] {
      background-image: url("../img/theme/apthefin/svg/img_plan_20per.svg");
      background-repeat: no-repeat;
      background-size: cover;
    }

    &[data-per^="4"],
    &[data-per^="5"] {
      background-image: url("../img/theme/apthefin/svg/img_plan_40per.svg");
      background-repeat: no-repeat;
      background-size: cover;
    }

    &[data-per^="6"],
    &[data-per^="7"] {
      background-image: url("../img/theme/apthefin/svg/img_plan_60per.svg");
      background-repeat: no-repeat;
      background-size: cover;
    }

    &[data-per^="8"],
    &[data-per^="9"] {
      background-image: url("../img/theme/apthefin/svg/img_plan_80per.svg");
      background-repeat: no-repeat;
      background-size: cover;
    }

    &[data-per="100"] {
      background-image: url("../img/theme/apthefin/svg/img_plan_100per.svg");
      background-repeat: no-repeat;
      background-size: cover;
    }
  }

  &__per {
    padding: rem(6);
    font-size: rem(13);
    text-align: center;
    line-height: 1;
    color: $gray;
    letter-spacing: rem(-0.23);
    font-weight: 600;
    background: #fff;
    border-radius: rem(8);
    position: absolute;
    left: 50%;
    top: 17.6470588%;
    transform: translateX(-50%);
    white-space: nowrap;
    box-shadow: rem(0) rem(2) rem(8) rem(0) rgba(0, 0, 0, 0.16);
    z-index: 10;

    &:after {
      content: "";
      width: 0;
      height: 0;
      border-top: rem(7) solid #fff;
      border-left: rem(7) solid transparent;
      border-right: rem(7) solid transparent;
      border-bottom: rem(7) solid transparent;
      position: absolute;
      left: 50%;
      bottom: rem(-13);
      margin-left: rem(-7);
    }
  }

  &.-cerScore {
    padding-bottom: rem(45);

    .planRate__per {
      top: rem(-15);
      color: $black;
    }
  }
}

.planProgress {
  position: relative;
  overflow: hidden;
  width: calc(100% - rem(60));
  margin: rem(23) auto 0;

  &__bg {
    width: 100%;
    height: auto;
    aspect-ratio: 273 / 136;
    background: url("../img/theme/apthefin/svg/bg_bar.svg") no-repeat 0 0;
  }

  &__bar {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
  }

  &__mask {
    height: 10%;
    width: 10%;
    position: absolute;
    z-index: 10;
    background: red;
    mask-image: url("../img/theme/apthefin/svg/inner_bar.svg");
    left: 0;
    top: 0;
  }

  #myClip {
    transition: transform 1s .3s;
    transform: rotate(-180deg);
    transform-origin: center bottom;
  }
}

.planTarget {
  border: rem(1) solid #DDE3E7;
  border-radius: rem(10);
  overflow: hidden;
  display: block;

  &__info {
    padding: rem(16) rem(20);
    background: #F5F7F9;
    position: relative;
  }

  &__type {
    border-radius: rem(20);
    border: rem(1) solid #1C64F2;
    padding: rem(1) rem(6);
    color: #1C64F2;
    font-size: rem(10);
    font-weight: 700;
    line-height: rem(16);
    letter-spacing: rem(-0.4);
    display: inline-block;
  }

  &__name {
    font-size: rem(16);
    color: $black;
    font-weight: 600;
    line-height: 1.6;
    margin-top: rem(4);
    position: relative;
    @include ellipsis(2);
    width: calc(100% - rem(50));
  }

  &__size {
    margin-top: rem(2);
    font-size: rem(13);
    color: $gray;
    line-height: rem(18);
  }

  &__price {
    padding: rem(10) rem(20);
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__now {
    font-size: rem(14);
    font-weight: 500;
    line-height: 1;
    color: $gray;
  }

  &__won {
    font-size: rem(16);
    font-weight: 700;
    line-height: rem(26);
    color: $black;
  }

  &__link {
    position: absolute;
    right: rem(20);
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    align-items: center;
    font-size: rem(14);
    color: #6B7280;
    font-weight: 600;
    line-height: rem(18);

    &:visited {
      color: #6B7280;
    }

    &:after {
      content: "";
      @include iconArrow18x18(#6B7280);
      display: block;
    }
  }
}

.planSec {
  padding: rem(20);
  border: rem(1) solid #dde3e7;
  border-radius: rem(12);
  position: relative;

  &.-noBorder {
    border: none;
    padding: 0;
  }

  &.-notLink {
    position: relative;

    &:after {
      content: "데이터에 연결되어있지 않음 문구";
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      border-radius: rem(12);
      opacity: 0.8;
      background: var(--font-ffffff, #FFF);
      backdrop-filter: blur(rem(2));
      font-size: rem(16);
      line-height: 1.5;
      color: $black;
      font-weight: 500;
    }
  }

  &__tit {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: rem(18);
    color: $black;

    &Eff {
      position: relative;
      display: inline-block;

      &:after {
        content: "";
        display: block;
        width: 100%;
        position: absolute;
        left: 0;
        bottom: rem(4);
        height: rem(4);
        z-index: -1;
      }
    }

    &Num {
      color: #ff5353;
    }

    &Right {
      font-size: rem(18);
      color: $black;
      font-weight: 700;
      line-height: rem(26);
      display: flex;
      align-items: center;

      span {
        font-size: rem(16);
        font-weight: 500;
        line-height: rem(24);
      }

      &:after {
        content: "";
        display: block;
        @include iconArrow18x18();
      }

      &.-noLink {
        &:after {
          display: none
        }
      }
    }
  }

  &__subTit {
    font-size: rem(13);
    font-weight: 500;
    line-height: 1;
    color: #455057;
  }

  &.-hi,
  &.-low {
    .savegoGraph__topTxt {
      font-weight: 700;
      letter-spacing: rem(-0.23);
    }

    .planSec__titEff {
      color: #1C64F2
    }

    .savegoGraph__item {
      &.-left {
        .savegoGraph__topTxt {
          border: none;
          padding: 0;
          box-shadow: none;
          position: initial;
          color: #1C64F2;
          height: auto;
          width: auto;

          &:after {
            display: none
          }
        }

        .savegoGraph__bar {
          background-color: #1A56DB
        }

        .savegoGraph__inner {
          background-color: #3F83F8
        }
      }

      &.-center {

        .savegoGraph__topTxt,
        .savegoGraph__txt {
          color: #1F2A37
        }

        .savegoGraph__bar {
          background-color: #1F2A37
        }
      }

      &.-right {}
    }

    .savegoGraph__line {
      left: rem(20);
      transform: none;

      &.-active {
        width: calc(50% - rem(20));
      }
    }
  }

  &.-low {}
}

.planMore {
  font-size: rem(14);
  line-height: 1.3;
  font-weight: 500;
  color: $black;
  display: flex;
  align-items: center;

  &:after {
    content: "";
    @include iconBtnMore();
    display: block;
  }
}

.savegoGraph {
  margin: rem(30) auto 0;
  width: calc(100% - 27%);
  height: rem(170);
  display: flex;
  justify-content: space-between;
  text-align: center;
  position: relative;

  &__inner {
    color: #fff;
    font-size: rem(13);
    font-weight: 700;
    line-height: 1;
    letter-spacing: rem(-0.23);
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    border-radius: rem(4);
    display: flex;
    justify-content: center;
    align-items: center;

    &.-low {
      span {
        position: absolute;
        top: rem(-15);
        color: #333;
      }
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;


  }

  &__topTxt {
    font-size: rem(13);
    line-height: rem(20);
    letter-spacing: rem(-0.23);
  }

  &__bar {
    width: rem(40);
    border-radius: rem(4);
    background: #dde3e7;
    height: 0%;
    max-height: rem(122);
    position: relative;
    margin: 0 auto;
    transition: height .5s;
    overflow: hidden;
  }

  &__txt {
    font-size: rem(13);
    font-weight: 700;
    color: #17181a;
    line-height: rem(20);
    letter-spacing: rem(-0.23);
  }

  &__line {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 0;
    width: 0;
    height: rem(1);
    border-top: rem(1) dashed $black;
    transition: width .3s;

    &.-active {
      width: calc(100% - rem(40));
    }
  }

  .-left {
    .savegoGraph__topTxt {
      border-radius: rem(4);
      background: #fff;
      font-size: rem(13);
      width: rem(45);
      height: rem(21);
      display: flex;
      justify-content: center;
      align-items: center;
      color: #6f7a81;
      position: relative;
      box-shadow: rem(0) rem(2) rem(6) rem(0) rgba(0, 0, 0, 0.16);
      z-index: 10;
      bottom: rem(10);

      &:after {
        content: "";
        display: block;
        border-top: rem(7) solid #fff;
        border-left: rem(7) solid transparent;
        border-right: rem(7) solid transparent;
        border-bottom: rem(7) solid transparent;
        position: absolute;
        left: 50%;
        margin-left: rem(-7);
        bottom: rem(-13);
      }
    }
  }

  //지출 적정
  @at-root .planSec.-good {
    .planSec__titEff {
      color: #12CA98;

      &:after {
        background-color: #D0F9ED;
      }
    }

    .-left {
      .savegoGraph__bar {
        background: #DEF0EB
      }

      .savegoGraph__inner {
        background: #12CA98;
      }
    }

    .savegoInfo__today:before {
      background: #12CA98
    }

    .savegoInfo__per {
      color: #12CA98
    }
  }

  //지출 과다
  @at-root .planSec.-bad {


    .planSec__titEff {
      color: #FF3E3E;

      &:after {
        background-color: #F6E5E5
      }
    }

    .-left {
      &.bad {
        .savegoGraph__bar {
          background: #F6E5E5;
          //max-height: rem(150);
          overflow: visible;
        }

        .savegoGraph__topTxt {
          height: auto;
          font-size: rem(12);
          line-height: 1.2;
          word-break: keep-all;
        }
      }

      .savegoGraph__bar {
        background: #F6E5E5;
      }

      .savegoGraph__inner {
        background: #FF3E3E
      }

    }

    .savegoInfo__today:before {
      background: #FF3E3E
    }

    .savegoInfo__per {
      color: #FF3E3E
    }
  }

  //30대 기준
  @at-root .planSec.-year {
    .-center {
      .savegoGraph__bar {
        background: #DDE3E7
      }
    }

    .-right {
      .savegoGraph__bar {
        background: #172733
      }
    }
  }

  //내평균
  @at-root .planSec.-my {
    .-center {
      .savegoGraph__bar {
        background: #172733
      }
    }

    .-right {
      .savegoGraph__bar {
        background: #DDE3E7
      }
    }
  }

  //신용점수 뷰페이지
  &.-viewPage {
    .-right {
      color: #B0B9C0;

      .savegoGraph__txt {
        color: #B0B9C0;
      }
    }
  }
}

.planBanner {
  display: block;
  border-radius: rem(10);
  padding: rem(20);
  background: #fcf3f0 url("../img/theme/apthefin/banner_plan.png") no-repeat calc(100% - rem(30)) calc(100% - rem(20));
  background-size: rem(160) rem(160);
  width: 100%;
  aspect-ratio: 327 / 276;

  &__tit {
    font-size: rem(20);
    color: $black;
    font-weight: 500;
    line-height: 1.4;

    strong {
      font-weight: 700;
      color: #FC603B;
    }
  }

  &__btn {
    color: #fc603b;
    font-size: rem(13);
    margin-top: rem(6);
    display: flex;
    align-items: center;
    font-weight: 500;

    &:after {
      content: "";
      width: rem(16);
      height: rem(16);
      background: url("../img/theme/apthefin/icon/ic_banner_arrow.png") no-repeat 0 0;
      background-size: cover;
      display: inline-block;
    }
  }
}

.planCaution {
  margin: rem(30) rem(-20) rem(-20);
  padding: rem(24) rem(24) rem(40);
  background: #f5f7f9;

  &__tit {
    font-size: rem(14);
    color: $gray;
    line-height: 1.3;
    font-weight: 500;
  }

  &__list {
    margin-top: rem(4);

    li {
      display: block;
      padding-left: rem(6);
      font-size: rem(12);
      color: $gray;
      position: relative;
      line-height: 1.3;

      &:before {
        content: "";
        display: block;
        width: rem(2);
        height: rem(2);
        border-radius: 50%;
        background: $gray;
        position: absolute;
        left: 0;
        top: rem(6);
      }
    }
  }
}

.borrowgoProgress {
  position: relative;
  @include clearFix();

  &__bar {
    appearance: none;
    width: 100%;

    &::-webkit-progress-bar {
      background: #1C64F2;
      border-radius: rem(200);
      height: rem(12);
    }

    &::-webkit-progress-value {
      background: #C3DDFD;
      border-radius: rem(200);
      height: rem(12);
    }
  }

  &__txt {
    display: inline-block;
    margin-top: rem(4);
    font-size: rem(12);
    line-height: 1;
    font-weight: 500;

    &.-left {
      color: #3F83F8;
      float: left;
    }

    &.-right {
      float: right;
      color: #1C64F2;
      text-align: right;
    }
  }
}

.borrowgoBox {
  padding: rem(16) rem(24);
  background: #F5F8FC;
  border-radius: rem(10);
  margin-top: rem(30);

  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &:not(:first-of-type) {
      border-top: rem(1) solid #dde3e7;
      padding-top: rem(10);
      margin-top: rem(10);
    }
  }

  &__tit {
    font-size: rem(14);
    line-height: rem(18);
    color: #455057;
  }

  &__num {
    font-size: rem(16);
    font-weight: 600;
    line-height: rem(26);
    color: $black;

    span {
      font-size: rem(14);
      font-weight: 500;
      margin-left: rem(2)
    }

    &.-link {
      position: relative;
      display: flex;
      align-items: center;

      &:after {
        content: "";
        display: block;
        @include iconArrow18x18();
      }
    }
  }
}

.collectgo {
  &__item {
    display: flex;
    align-items: center;

    &:not(:first-of-type) {
      margin-top: rem(20);
    }

    &:nth-child(1) {
      .collectgo__bar {
        background-color: #C3DDFD;
      }
    }

    &:nth-child(2) {
      .collectgo__bar {
        background-color: #E1EFFE;
      }
    }

    &:nth-child(3) {
      .collectgo__bar {
        background-color: #EBF5FF;
      }
    }

    &.-self {
      .collectgo__img {
        background-color: #9061F9
      }

      .collectgo__bar {
        background-color: #DCD7FE
      }
    }

    &.-lease {
      .collectgo__img {
        background-color: #3F83F8
      }

      .collectgo__bar {
        background-color: #C3DDFD
      }
    }

    &.-monthly {
      .collectgo__img {
        background-color: #0694A2
      }

      .collectgo__bar {
        background-color: #AFECEF
      }
    }

    .chkBox {
      display: none;
      position: absolute;
      right: 0;
    }
  }

  &__img {
    display: flex;
    width: rem(40);
    height: rem(40);
    border-radius: 50%;
    overflow: hidden;
    margin-right: rem(10);
    justify-content: center;
    align-items: center;

    img {
      display: block;
      width: 100%;
      height: 100%
    }

    color:#fff;
    font-size:rem(13);
    font-weight:700;
    line-height:1;
    flex-shrink: 0;
  }

  &__tit {
    font-size: rem(13);
    font-weight: 500;
    color: #455057;
    line-height: 1;
    @include ellipsis();
  }

  &__box {
    width: calc(100% - rem(50));
  }

  &__bar {
    margin-top: rem(4);
    height: rem(26);
    border-radius: rem(4);
    position: relative;
    padding: rem(4) 0;
    box-sizing: border-box;
    width: 0%;
    transition: width .5s;
  }

  &__txt {
    text-align: right;
    font-size: rem(14);
    line-height: 1.3;
    color: $black;
    font-weight: 500;
    white-space: nowrap;
    padding: 0 rem(10);
  }

  &.-noBar {
    .collectgo__bar {
      width: 0 !important;
      height: auto;
      padding: 0;
    }

    .collectgo__txt {
      padding: 0;
      font-size: rem(16);
      line-height: 1.3;
    }
  }

  &Tit {
    &__bank {
      display: flex;
      margin-bottom: rem(4);
      font-size: rem(16);
      font-weight: 500;
      line-height: 1;
      color: #455057;
      align-items: center;

      .plan__btnTooltip {
        margin-left: rem(4);
      }
    }

    &__num {
      font-size: rem(30);
      color: #333;
      font-weight: 700;
      line-height: rem(36);
      letter-spacing: rem(-0.6);
    }

    &__won {
      font-size: rem(18);
      font-weight: 700;
      line-height: rem(26);
      color: #333;
    }
  }

  &__allSelect {
    position: absolute;
    top: 0;
    right: 0;
    font-size: rem(13);
    font-weight: 500;
    line-height: 1;
    color: #455057;
    display: none;
  }
}


.savegoInfo {
  border: rem(1) solid #D8DEE7;
  border-radius: rem(8);
  padding: rem(18) rem(20);

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: rem(1) solid #d8dee7;
    padding-bottom: rem(10);
    margin-bottom: rem(10);
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
